html,
body {
  margin: 0;
  padding: 0;
  font-size: 0.83rem;
  font-weight: 300;
  line-height: 1;
  font-size: 10pt;
  -webkit-text-size-adjust: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #666;
  -ms-word-break: break-word;
  word-break: break-word;
  -moz-word-break: break-word;
  -webkit-word-break: break-word;
  -webkit-hyphens: none;
  -moz-hyphens: none;
  hyphens: none;
  letter-spacing: 0.2pt;
}

* {
  box-sizing: border-box;
}

a {
  border-bottom: 0;
  text-decoration: none;
  cursor: pointer;
}

main {
  margin-bottom: 50px;
}

img {
  max-width: 100%;
}

header {
  padding: 100px 0;
  text-align: center;
  color: #000;
}

header h1 {
  font-family: 'Cormorant Garamond';
  font-size: 3.75rem;
  font-weight: 300;
  margin: 0;
}

header p {
  font-family: Goergia;
  margin-bottom: 0;
}

.container {
  width: 100%;
  padding-right: 10px;
  padding-left: 10px;
  margin-right: auto;
  margin-left: auto;
}

article {
  margin-bottom: 50px;
}

.card {
  position: relative;
  padding-bottom: 15px;
  padding-inline-start: 25px;
  padding-left: 25px;
  padding-right: 25px;
  padding-top: 0;

  box-shadow: 0px 4px 14px -4px rgba(0, 0, 0, 0.43);
}
.card figure {
  margin: 0;
}

/* .card .card-header {
  width: calc(100% + 50px);
  margin-top: -25px;
  margin-left: -25px;
} */

.card img,
.card iframe,
.card video {
  width: calc(100% + 50px) !important;
  margin: 0 -25px;
  max-width: inherit !important;
}

.card p,
.card a {
  font-family: Helvetica, Arial, sans-serif;
  font-weight: 300;
  line-height: 180%;
  color: #666;
}

.card .card-body > div:nth-child(1) > p:first-child {
  padding-top: 1em;
}

/* Extra Small (xs) (primitive) */
@media (min-width: 320px) {
}

/* Small (sm) */
@media (min-width: 640px) {
}

/* Medium (md) */
@media (min-width: 768px) {
  .container {
    width: 80%;
  }
}

/* Large (lg) */
@media (min-width: 1024px) {
  .container {
    width: 50%;
  }
}

/* Extra Large (xl) */
@media (min-width: 1280px) {
}

.card-datetime {
  font-size: 0.75rem;
  font-family: georgia;
  font-stretch: 100%;
  font-style: italic;
  font-variant-caps: normal;
  font-variant-east-asian: normal;
  font-variant-ligatures: normal;
  font-variant-numeric: normal;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  margin-bottom: 0;
}

.infinite-scroll-component {
  overflow: initial !important;
}

/* Tumblr */
blockquote {
  quotes: none;
}
blockquote:after,
blockquote:before {
  content: '';
  content: none;
}
i {
  font-style: oblique !important;
}
blockquote {
  border-left: 1px solid #aeaeae;
  margin-left: 2px;
  padding-left: 10px;
}
.card blockquote p:not(.tmblr-attribution) {
  margin: 10px 0;
  margin-bottom: 0;
}
.card > blockquote {
  margin: -10px 0;
}
/*! CSS Used from: Embedded */
blockquote {
  border-left: 1px solid #535353;
}
